const radioTableView = document.querySelectorAll('.js-radioTable');
const tables = document.querySelectorAll('.js-table');

const toggle = (event) => {
  const self = event.target;
  const target = self.value;

  tables.forEach(table =>
      table.classList.remove('table--show'));

  const table = document.getElementById(target);
  table.classList.toggle('table--show');
};

radioTableView.forEach(radio =>
    radio.addEventListener('click', () => toggle(event)));
