const customer = document.querySelector('.js-customer')
const customerSearch = document.querySelector('.js-customerSearch')
const clearFieldCustomer = document.querySelector('.js-clearFieldCustomer')

const autoCustomer = document.querySelector('.js-autoCustomer')
const autoCustomerSearch = document.querySelector('.js-autoCustomerSearch')
const clearFieldAutoCustomer = document.querySelector('.js-clearFieldAutoCustomer')

const searchCustomer = (e) => {
  const searchName = e.target.value
  const api = `https://crm.atlantm.com/esoft/test-drive/requests/searchentitieshttp.php?deal_title=${searchName}`
  const contacts = []

  if (searchName.length < 2) {
    customerSearch.innerHTML = ''

    return false
  } else {
    fetch(api)
      .then((response) => response.json())
      .then((blob) => {
        contacts.push(...blob)
      })
      .finally(() => {
        customerSearch.innerHTML = contacts
          .map((match) => {
            const customer = `${match.TITLE}`

            return `<li class="search__item js-addCustomer" data-name="${customer.trim()}" title="${customer.trim()}">
                ${match.TITLE}
              </li>`
          })
          .join('')
      })
  }
}

customer.addEventListener('keyup', (e) => searchCustomer(e))

document.addEventListener(
  'click',
  (event) => {
    if (event.target.classList.contains('js-addCustomer')) {
      addSearchCustomer(`${event.target.dataset.name}`)
    }
  },
  false
)

const addSearchCustomer = (name) => {
  console.info(name)
  customer.value = name
  customerSearch.innerHTML = ''
}

clearFieldCustomer.addEventListener('click', (evt) => {
  customer.value = ''
  customerSearch.innerHTML = ''
  return false
})

const searchAutoCustomer = (e) => {
  const searchName = e.target.value
  const api = `https://crm.atlantm.com/esoft/test-drive/requests/searchentitieshttp.php?assigned_name=${searchName}`
  const contacts = []

  if (searchName.length < 2) {
    autoCustomerSearch.innerHTML = ''

    return false
  } else {
    fetch(api)
      .then((response) => response.json())
      .then((blob) => {
        contacts.push(...blob)
      })
      .finally(() => {
        autoCustomerSearch.innerHTML = contacts
          .map((match) => {
            const fio = `${match.LAST_NAME} ${match.NAME} ${match.SECOND_NAME}`

            return `<li class="search__item js-addAutoCustomer" data-name="${fio.trim()}" title="${fio.trim()}">
                ${match.LAST_NAME} ${match.NAME} ${match.SECOND_NAME}
              </li>`
          })
          .join('')
      })
  }
}

autoCustomer.addEventListener('keyup', (e) => searchAutoCustomer(e))

document.addEventListener(
  'click',
  (event) => {
    if (event.target.classList.contains('js-addAutoCustomer')) {
      addSearchAutoCustomer(`${event.target.dataset.name}`)
    }
  },
  false
)

const addSearchAutoCustomer = (name) => {
  console.info(name)
  autoCustomer.value = name
  autoCustomerSearch.innerHTML = ''
}

clearFieldAutoCustomer.addEventListener('click', (evt) => {
  autoCustomer.value = ''
  autoCustomerSearch.innerHTML = ''
  return false
})
