const modalTriggers = document.querySelectorAll('.js-modalOpen')

modalTriggers.forEach((trigger) => {
  trigger.addEventListener('click', (evt) => {
    const popupTrigger = trigger.dataset.popupTrigger
    const popupModal = document.querySelector(`[data-popup-modal="${popupTrigger}"]`)
    const bodyBlackout = popupModal.querySelector('.js-modalBlackout')
    const modalCloseBtn = popupModal.querySelector('.js-modalCloseBtn')
    evt.preventDefault()
    popupModal.classList.add('is--visible')
    bodyBlackout.classList.add('is-blacked-out')

    bodyBlackout.addEventListener('click', () => {
      popupModal.classList.remove('is--visible')
      bodyBlackout.classList.remove('is-blacked-out')
    })

    modalCloseBtn.addEventListener('click', () => {
      popupModal.classList.remove('is--visible')
      bodyBlackout.classList.remove('is-blacked-out')
    })

    window.addEventListener('keydown', (evt) => {
      if (evt.keyCode === 27) {
        if (popupModal.classList.contains('is--visible')) {
          evt.preventDefault()
          popupModal.classList.remove('is--visible')
          bodyBlackout.classList.remove('is-blacked-out')
        }
      }
    })
  })
})
