import * as Lightpick from 'Lightpick';
import moment from 'moment';

const prevMonth = moment();

const datePicker = document.querySelector('.js-datePicker');
const datePickerFrom = document.querySelector('.js-dateCreationFrom');
const datePickerTo = document.querySelector('.js-dateCreationTo');
const datePickerFutureContact = document.querySelector('.js-dateFutureContact');

const datePickerCalendar = new Lightpick({
  field: datePicker,
  format: 'DD.MM.YYYY',
  startDate: prevMonth.format('DD.MM.YYYY'),
  onSelect: function(date) {
    console.info(date.format('DD.MM.YYYY'));
  },
});

const datePickerCalendarFrom = new Lightpick({
  field: datePickerFrom,
  format: 'DD.MM.YYYY',
  startDate: prevMonth.format('DD.MM.YYYY'),
  onSelect: function(date) {
    console.info(date.format('DD.MM.YYYY'));
  },
});

const datePickerCalendarTo = new Lightpick({
  field: datePickerTo,
  format: 'DD.MM.YYYY',
  startDate: prevMonth.format('DD.MM.YYYY'),
  onSelect: function(date) {
    console.info(date.format('DD.MM.YYYY'));
  },
});

const datePickerCalendarFutureContact = new Lightpick({
  field: datePickerFutureContact,
  format: 'DD.MM.YYYY',
  orientation: 'top',
  startDate: prevMonth.format('DD.MM.YYYY'),
  onSelect: function(date) {
    console.info(date.format('DD.MM.YYYY'));
  },
});

export {datePickerCalendar};
