const btnSectionInfo = document.querySelectorAll('.js-btnShowRecordingSection');

const toggle = (event) => {
  const self = event.currentTarget;
  const target = self.dataset.target;
  const sectionInfo = document.getElementById(target);
  sectionInfo.classList.toggle('recording-section__info--show');

  const btnIcon = self.querySelector('.button__collapse-icon')
  btnIcon.classList.toggle('button__collapse-icon--active')
};

btnSectionInfo.forEach(btn =>
    btn.addEventListener('click', () => toggle(event)));
