import moment from 'moment'
import { datePickerCalendar } from './datePicker'

export const table = {
  load() {
    table.data.load()
  },
  data: {
    $table: document.querySelector('.js-tableH'),
    $reservationResourcesBox: document.querySelector('.js-reservationResources'),
    reservationResourcesList: [],

    load() {
      const self = this

      self.registerEventListeners()
    },

    registerEventListeners() {
      const self = this

      self.$table.addEventListener('click', (e) => {
        if (e.target && e.target.classList.contains(`js-recordInfoTime`)) {
          self.handleClickPrerecord(e)
        }
      })

      document.addEventListener('click', (e) => {
        if (e.target && e.target.classList.contains(`js-deletePrerecord`)) {
          self.handleClickDeletePrerecord(e)
        }
      })

      document.addEventListener('click', (e) => {
        if (e.target && e.target.classList.contains(`js-reservationResourcesCleanse`)) {
          self.handleClickDeleteAllPrerecords()
        }
      })
    },

    handleClickPrerecord(e) {
      const self = this

      const element = e.target
      const prerecordId = element.dataset.slot_id

      if (element.classList.contains(`table__cell--active`)) {
        self.removePrerecord(prerecordId)
      } else {
        self.addPrerecord(element)
      }

      self.drawReservationResourcesBox()
    },

    addPrerecord(element) {
      const self = this

      element.classList.add('table__cell--active')

      const prerecord = {
        index: element.dataset.index,
        slotId: element.dataset.slot_id,
        userId: element.dataset.user_id,
        name: element.parentElement.querySelector('.js-recordInfoName').innerHTML,
        title: element.dataset.title,
        time: `${element.dataset.hour}:${element.dataset.minutes}`,
        period: 20,
      }

      self.reservationResourcesList = [...self.reservationResourcesList, prerecord]
    },

    removePrerecord(slotId) {
      const self = this
      const $tableActiveCells = self.$table.querySelectorAll('.table__cell--active')

      self.reservationResourcesList = self.reservationResourcesList.filter(
        (reservation) => reservation.slotId !== slotId
      )

      $tableActiveCells.forEach(($activeCell) => {
        const activeCellId = $activeCell.dataset.slot_id

        if (slotId === activeCellId) $activeCell.classList.remove('table__cell--active')
      })
    },

    drawReservationResourcesBox() {
      const self = this

      const selectedDate = datePickerCalendar.getDate().format('DD.MM.YYYY')

      const groupedReservationResourcesList = self.reservationResourcesList
        .sort((a, b) => {
          const userIdA = parseInt(a.userId)
          const userIdB = parseInt(b.userId)
          const indexA = parseInt(a.index)
          const indexB = parseInt(b.index)

          if (userIdA < userIdB) return -1
          if (userIdA > userIdB) return 1

          if (indexA < indexB) return -1
          if (indexA > indexB) return 1

          return 0
        })
        .reduce((result, currentValue, index) => {
          const found = result.find(
            (p) => p.userId === currentValue.userId && p.index.includes(parseInt(currentValue.index) - 1)
          )

          if (!found) return [...result, currentValue]

          const other = result.filter((p) => p !== found)

          return [
            ...other,
            {
              ...found,
              index: `${found.index}, ${currentValue.index}`,
              slotId: `${found.slotId}, ${currentValue.slotId}`,
              time: `${found.time} - ${currentValue.time}`,
              period: found.period + currentValue.period,
            },
          ]
        }, [])

      const timeConvert = (n) => {
        const hours = n / 60
        const rHours = Math.floor(hours)
        const minutes = (hours - rHours) * 60
        const rMinutes = Math.round(minutes)
        return rHours < 1 ? `${rMinutes}мин.` : rMinutes < 1 ? `${rHours}ч.` : `${rHours}ч. ${rMinutes}мин.`
      }

      self.$reservationResourcesBox.innerHTML = ''
      groupedReservationResourcesList.map((item) => {
        const title = item.name
        const dateTime = `${selectedDate}<br/> <strong>${item.time.split(' - ').shift()} - ${moment(
          item.time.split(' - ').shift(),
          'HH:mm'
        )
          .add(parseInt(item.period), 'minutes')
          .format('HH:mm')}</strong>`

        const period = timeConvert(parseInt(item.period))

        self.$reservationResourcesBox.innerHTML += `
          <li class="list-answers__item list-answers__item--reservation">
            <button class="button button--close list-answers__button js-deletePrerecord"
             data-id="${item.slotId}"
              type="button">
                <svg aria-hidden="true" xmlns="http://www.w3.org/2000/svg" viewBox="0 0 352 512" width="15" height="15">
                    <path fill="currentColor" d="M242.72 256l100.07-100.07c12.28-12.28 12.28-32.19 0-44.48l-22.24-22.24c-12.28-12.28-32.19-12.28-44.48 0L176 189.28 75.93 89.21c-12.28-12.28-32.19-12.28-44.48 0L9.21 111.45c-12.28 12.28-12.28 32.19 0 44.48L109.28 256 9.21 356.07c-12.28 12.28-12.28 32.19 0 44.48l22.24 22.24c12.28 12.28 32.2 12.28 44.48 0L176 322.72l100.07 100.07c12.28 12.28 32.2 12.28 44.48 0l22.24-22.24c12.28-12.28 12.28-32.19 0-44.48L242.72 256z"></path>
                </svg>
            </button>
            <span class="list-answers__title list-answers__title--name" title="${title}">${title}</span>
            <span class="list-answers__title list-answers__title--time">${dateTime}</span>
            <span class="list-answers__title list-answers__title--period">План<br/> <strong>${period}</strong></span>
          </li>`
      })
    },

    handleClickDeletePrerecord(e) {
      const self = this
      const $prerecord = e.target.closest('li')
      const prerecordIds = e.target.dataset.id.split(', ')

      $prerecord.remove()
      prerecordIds.forEach((id) => self.removePrerecord(id))
    },

    handleClickDeleteAllPrerecords() {
      const self = this
      const $tableActiveCells = self.$table.querySelectorAll('.table__cell--active')

      self.reservationResourcesList = []

      $tableActiveCells.forEach(($activeCell) => {
        $activeCell.classList.remove('table__cell--active')
      })

      self.drawReservationResourcesBox()
    },
  },
}
